import React, { useEffect, useState } from 'react'
import { useLazyGetApllicationQuery } from '../store/api/ApplicationAPI'
import { useNavigate } from 'react-router-dom';
import toast from 'react-hot-toast';

const VisaStatus = () => {

    const [id, setId] = useState("")

    const navigate = useNavigate();

    const [getApllication, { data, isError, error, isLoading, isSuccess }] = useLazyGetApllicationQuery()

    const inputHandler = (e) => {
        setId(e.target.value)
    }

    useEffect(() => {
        if (isError) {
            toast.error(error.data.message);
        }
        if (isSuccess) {
            navigate(`/application/${id}`)
        }
    }, [isError, isSuccess]);
    const btnHandler = async (e) => {
        e.preventDefault();
        await getApllication(id)
    }

    return (
        <div>
            <div className="container">
                <div className="row pt-5 pb-5 justify-content-center">
                    <div className="col-md-6">
                        <h2 className="text-uppercase">Visa Status</h2>
                        <div className="border auth-form border-1 p-3 ">
                            <form action="">
                                <div className="pt-3">
                                    <label htmlFor="" className="form-label fw-bold">
                                        Application Number
                                    </label>
                                    <input
                                        className="form-control rounded-0"
                                        type="text"
                                        name="_id"
                                        placeholder="Please Enter Application Number"
                                        onChange={(e) => inputHandler(e)}
                                    />
                                </div>
                                <div className="pt-3">
                                    <label htmlFor="" className="form-label fw-bold">
                                        Email
                                    </label>
                                    <input
                                        className="form-control rounded-0"
                                        type="email"
                                        name="email"
                                        placeholder="Please Enter Your Email"
                                    />
                                </div>
                                <div className="pt-3">
                                    <button className="btn btn-primary" onClick={btnHandler}>
                                        Check Status
                                    </button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default VisaStatus