import React, { useState } from 'react'
import ApplicationHeader from '../components/ApplicationHeader'
import { Link } from "react-router-dom"
import { setApplication } from '../store/slice/ApplicationSlice'
import { useDispatch, useSelector } from 'react-redux'


const SecondStage = () => {
    const dispatch = useDispatch()
    const { application } = useSelector((state) => (state.application))
    const [formData, setFromData] = useState(application)

    const changeHandler = (e) => {
        dispatch(setApplication({ ...formData, [e.target.name]: e.target.value }))
        setFromData({ ...formData, [e.target.name]: e.target.value })


    }
    return (
        <div>
            <ApplicationHeader FirstStage="active-on" SecondStage="active-on" />
            <div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="email">Email</label>
                        <input className='form-control' type="email" name="email" id="email" value={formData.email} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="education">Education</label>
                        <input className='form-control' type="text" name="education" id="education" value={formData.education} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="contractyear">Contract Duration Year</label>
                        <input className='form-control' type="text" name="contractyear" id="contractyear" value={formData.contractyear} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="probationmonth">No Of Probation Months</label>
                        <input className='form-control' type="text" name="probationmonth" id="probationmonth" value={formData.probationmonth} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="basicsalary">Basic Salary</label>
                        <input className='form-control' type="text" name="basicsalary" id="basicsalary" value={formData.basicsalary} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="houseallowance">House Allowance</label>
                        <input className='form-control' type="text" name="houseallowance" id="houseallowance" value={formData.houseallowance} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="transportallowance">Transport Allowance</label>
                        <input className='form-control' type="text" name="transportallowance" id="transportallowance" value={formData.transportallowance} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="otherallowance">Other Allowance</label>
                        <input className='form-control' type="text" name="otherallowance" id="otherallowance" value={formData.otherallowance} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="totalsalary">Total Salary</label>
                        <input className='form-control' type="text" name="totalsalary" id="totalsalary" value={formData.totalsalary} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="ticketduration">Ticket Duration</label>
                        <input className='form-control' type="text" name="ticketduration" id="ticketduration" value={formData.ticketduration} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2 ">
                    <div className='col-md-6'>
                        <Link className='btn btn-primary' to={"/apply/first"} >Previous</Link>
                    </div>
                    <div className='col-md-6 text-end'>
                        <Link className='btn btn-primary' to={"/apply/third"} >Next</Link>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default SecondStage