import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    application: {
        firstname: "",
        lastname: "",
        dateofbirth: "",
        placeofbirth: "",
        passportnation: "",
        passportno: "",
        passportexpirydate: "",
        phoneno: "",
        gender: "",
        sponsername: "",
        purposeofvisa: "",
        profession: "",
        email: "",
        education: "",
        contractyear: "",
        probationmonth: "",
        basicsalary: "",
        houseallowance: "",
        transportallowance: "",
        foodallowance: "",
        otherallowance: "",
        totalsalary: "",
        ticketduration: "",
    }
};

const ApplicationSlice = createSlice({
    name: "ApplicationSlice",
    initialState,
    reducers: {
        setApplication(state, action) {
            state.application = action.payload;
            console.log(action.payload);

        },
    },
});

export const { setApplication } = ApplicationSlice.actions;
export default ApplicationSlice.reducer;
