import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";
import { setIsAuthorized, setLoading } from "../slice/UserSlice";

export const AuthAPI = createApi({
    reducerPath: "AuthAPI",
    baseQuery: fetchBaseQuery({ baseUrl: "/api/v1/user" }),
    tagTypes: ["user"],
    endpoints: (bulider) => ({
        login: bulider.mutation({
            query: ({ body }) => ({
                url: "/login",
                method: "POST",
                body,
            }),
            async onQueryStarted(args, { dispatch, queryFulfilled }) {
                try {
                    await queryFulfilled;
                    await dispatch(AuthAPI.endpoints.getProfile.initiate(null));
                } catch (error) {
                    console.log(error);
                }
            },
        }),
        getProfile: bulider.query({
            query: () => ({
                url: "/profile",
            }),
            providesTags: ["user"],
            async onQueryStarted(arg, { dispatch, queryFulfilled }) {
                try {
                    const { data } = await queryFulfilled;
                    dispatch(setIsAuthorized(true));
                    dispatch(setLoading(false));
                } catch (error) {
                    dispatch(setIsAuthorized(false));
                    dispatch(setLoading(false));
                    console.log(error);
                }
            },
        }),

    }),
});

export const { useLoginMutation, useGetProfileQuery } = AuthAPI;
