import React, { useState } from 'react'
import ApplicationHeader from '../components/ApplicationHeader'
import { Link } from "react-router-dom"
import { setApplication } from '../store/slice/ApplicationSlice'
import { useSelector, useDispatch } from 'react-redux'

const FirstStage = () => {

  const dispatch = useDispatch()
  const { application } = useSelector((state) => (state.application))
  const [formData, setFromData] = useState(application)

  const changeHandler = (e) => {
    dispatch(setApplication({ ...formData, [e.target.name]: e.target.value }))
    setFromData({ ...formData, [e.target.name]: e.target.value })


  }
  return (
    <div>
      <ApplicationHeader FirstStage="active-on" />
      <div>
        <div className="row p-2">
          <div className='col-md-6'>
            <label htmlFor="firstname">First Name</label>
            <input className='form-control' type="text" name="firstname" id="firstname" onChange={(e) => changeHandler(e)} />
          </div>
          <div className='col-md-6'>
            <label htmlFor="">Last Name</label>
            <input className='form-control' type="text" name="lastname" id="lastname" value={formData.lastname} onChange={(e) => changeHandler(e)} />
          </div>
        </div>
        <div className="row p-2">
          <div className='col-md-6'>
            <label htmlFor="dateofbirth">Date Of Birth</label>
            <input className='form-control' type="text" name="dateofbirth" id="dateofbirth" value={formData.dateofbirth} onChange={(e) => changeHandler(e)} />
          </div>
          <div className='col-md-6'>
            <label htmlFor="placeofbirth">Place Of Birth</label>
            <input className='form-control' type="text" name="placeofbirth" id="placeofbirth" value={formData.placeofbirth} onChange={(e) => changeHandler(e)} />
          </div>
        </div>
        <div className="row p-2">
          <div className='col-md-6'>
            <label htmlFor="passportnation">Passport Nationality</label>
            <input className='form-control' type="text" name="passportnation" id="passportnation" value={formData.passportnation} onChange={(e) => changeHandler(e)} />
          </div>
          <div className='col-md-6'>
            <label htmlFor="passportno">Passport Number</label>
            <input className='form-control' type="text" name="passportno" id="passportno" value={formData.passportno} onChange={(e) => changeHandler(e)} />
          </div>
        </div>
        <div className="row p-2">
          <div className='col-md-6'>
            <label htmlFor="passportexpirydate">Passport Expiry Date</label>
            <input className='form-control' type="text" name="passportexpirydate" id="passportexpirydate" value={formData.passportexpirydate} onChange={(e) => changeHandler(e)} />
          </div>
          <div className='col-md-6'>
            <label htmlFor="phoneno">Phone Number</label>
            <input className='form-control' type="text" name="phoneno" id="phoneno" value={formData.phoneno} onChange={(e) => changeHandler(e)} />
          </div>
        </div>
        <div className="row p-2">
          <div className='col-md-6'>
            <label htmlFor="gender">Gender</label>
            <input className='form-control' type="text" name="gender" id="gender" value={formData.gender} onChange={(e) => changeHandler(e)} />
          </div>
          <div className='col-md-6'>
            <label htmlFor="sponsername">Sponser Name</label>
            <input className='form-control' type="text" name="sponsername" id="sponsername" value={formData.sponsername} onChange={(e) => changeHandler(e)} />
          </div>
        </div>
        <div className="row p-2">
          <div className='col-md-6'>
            <label htmlFor="purposeofvisa">Purpose Of Visa</label>
            <input className='form-control' type="text" name="purposeofvisa" id="purposeofvisa" value={formData.purposeofvisa} onChange={(e) => changeHandler(e)} />
          </div>
          <div className='col-md-6'>
            <label htmlFor="profession">Profession</label>
            <input className='form-control' type="text" name="profession" id="profession" value={formData.profession} onChange={(e) => changeHandler(e)} />
          </div>
        </div>
        <div className="row p-2 ">
          <div className='col-md-6'>

          </div>
          <div className='col-md-6 text-end'>
            <Link className='btn btn-primary' to={"/apply/second"} >Next</Link>
          </div>
        </div>
      </div>
    </div>
  )
}

export default FirstStage