import React, { useRef } from 'react';
import { useReactToPrint } from 'react-to-print';
import { useParams } from 'react-router-dom';
import { useGetApllicationQuery } from '../store/api/ApplicationAPI';
import QRCode from "react-qr-code";

const PrintVisa = () => {

    const params = useParams()

    const { data, isLoading } = useGetApllicationQuery(params.id)


    const Print = () => {
        //console.log('print');  
        let printContents = document.getElementById('visa').innerHTML;
        let originalContents = document.body.innerHTML;
        document.body.innerHTML = printContents;
        window.print();
        document.body.innerHTML = originalContents;
    }


    return (
        <div className="container">
            <div className="visa my-5">
                <div className="row d-flex justify-content-center mb-5">
                    <button className="btn btn-success col-md-5" onClick={Print}>
                        <i className="fa fa-print" /> Download Visa
                    </button>
                </div>
                <div id='visa' className="p-3 border border-secondary">
                    <header className="clearfix">
                        <div id="logo">
                            <img src="https://technologyvala.com/0001.jpg" alt="Logo" />
                        </div>
                        <h3 className='pt-2 pb-4'>VISA NO # {data?.application._id}</h3>
                    </header>
                    <main>
                        <div>
                            <div className="row">
                                <div className="col-6">
                                    <p className='d-inline-block text-uppercase fs-4'>Name :</p>
                                    <p className='d-inline-block text-uppercase ps-2 fs-4'>{` ${data?.application.firstname} ${data?.application.lastname}`}</p>
                                </div>
                                <div className="col-6">
                                    <p className='d-inline-block text-uppercase fs-4'>Phone :</p>
                                    <p className='d-inline-block text-uppercase ps-2 fs-4'>{data?.application.phoneno}</p>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-6">
                                    <p className='d-inline-block text-uppercase fs-4'>Passport Nationality :</p>
                                    <p className='d-inline-block text-uppercase ps-2 fs-4'>{data?.application.passportnation}</p>
                                </div>
                                <div className="col-6">
                                    <p className='d-inline-block text-uppercase fs-4'>Passport Number :</p>
                                    <p className='d-inline-block text-uppercase ps-2 fs-4'>{data?.application.passportno}</p>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-6">
                                    <p className='d-inline-block text-uppercase fs-4'>Purpose Of Visa :</p>
                                    <p className='d-inline-block text-uppercase ps-2 fs-4'>{data?.application.purposeofvisa}</p>
                                </div>
                                <div className="col-6">
                                    <p className='d-inline-block text-uppercase fs-4'>Gender :</p>
                                    <p className='d-inline-block text-uppercase ps-2 fs-4'>{data?.application.gender}</p>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-6">
                                    <p className='d-inline-block text-uppercase fs-4'>Sponser Name :</p>
                                    <p className='d-inline-block text-uppercase ps-2 fs-4'>{data?.application.sponsername}</p>
                                </div>
                                <div className="col-6">
                                    <p className='d-inline-block text-uppercase fs-4'>Profession :</p>
                                    <p className='d-inline-block text-uppercase ps-2 fs-4'>{data?.application.profession}</p>
                                </div>
                            </div>

                            <QRCode
                                size={256}
                                style={{ height: "auto", maxWidth: "20%", width: "20%" }}
                                value={`${data?.fronturl}/print/${data?.application._id}`}
                                viewBox={`0 0 256 256`}
                            />
                        </div>
                    </main>
                </div>
            </div>
        </div>
    )
}

export default PrintVisa