import React from 'react'

const ApplicationHeader = ({ FirstStage, SecondStage, ThirdStage }) => {
  return (
    <div>
      <div>
        <img className='ms-2' src="https://technologyvala.com/header-img.jpeg" alt="" height={"60px"} srcset="" />

        <div className='stage row  justify-content-between'>
          <div className={`col-4 bg-primary stage-inner text-white text-center ${FirstStage}`}><p className='p-2'>First Stage <i class="fa-solid fa-angles-right"></i></p></div>
          <div className={`col-4 bg-primary stage-inner text-white text-center ${SecondStage}`}><p className='p-2'>Second Stage <i class="fa-solid fa-angles-right"></i></p></div>
          <div className={`col-4 bg-primary stage-inner text-white text-center ${ThirdStage}`}><p className='p-2'>Third Stage <i class="fa-solid fa-angles-right"></i></p></div>
        </div>
      </div>
    </div>
  )
}

export default ApplicationHeader