import React from 'react'
import { Link } from 'react-router-dom'

const Home = () => {
    return (
            <div className='home'>
                <div className="row h-100 justify-content-center align-items-center">
                    <div className="col-12 text-center">
                        <Link className='btn btn-primary home-btn' to={"/apply/first"}>Visa Apply</Link>n
                        <Link className='btn btn-primary home-btn' to={"/status"}>Apply Status</Link>n
                        <Link className='btn btn-primary home-btn' to={"Login"}>Login</Link>n
                    </div>
                </div>
            </div>
    )
}

export default Home