import React from 'react'
import { useGetApllicationQuery } from '../store/api/ApplicationAPI'
import Loader from '../components/Loader'
import { useNavigate, useParams, Link } from 'react-router-dom'
import { useSelector } from 'react-redux'

const VisaApplication = () => {
    const navigate = useNavigate()
    const { isAuthorized } = useSelector((state) => state.user);

    const params = useParams()

    const { data, isLoading } = useGetApllicationQuery(params.id)

    if (isLoading) {
        return <Loader />
    }

    return (
        <div>

            <div className="row p-2 ">
                <div className='col-md-6'>
                    {isAuthorized && <Link className='btn btn-primary' to={"/dashboard"} >Back To Dashboard</Link>}
                </div>
                <div className='col-md-6 text-end'>
                    <Link className='btn btn-primary' to={`/print/${data?.application._id}`} >Print</Link >
                </div>
            </div>

            <div className='row p-2'>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Application Status :</p>
                    <p className={`fw-bold d-inline-block ${data.application.apllicationstatus == "reject" ? "text-danger" : "text-success"}`} >{data.application.apllicationstatus}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>First Name :</p>
                    <p className='d-inline-block'>{data.application.firstname}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Last Name :</p>
                    <p className='d-inline-block'>{data.application.lastname}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Date Of Birth :</p>
                    <p className='d-inline-block'>{data.application.dateofbirth}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Place Of Birth :</p>
                    <p className='d-inline-block'>{data.application.placeofbirth}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Passport Nationality :</p>
                    <p className='d-inline-block'>{data.application.passportnation}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Passport Number :</p>
                    <p className='d-inline-block'>{data.application.passportno}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Passport Expiry Date :</p>
                    <p className='d-inline-block'>{data.application.passportexpirydate}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Phone Number :</p>
                    <p className='d-inline-block'>{data.application.phoneno}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Gender :</p>
                    <p className='d-inline-block'>{data.application.gender}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Sponser Name :</p>
                    <p className='d-inline-block'>{data.application.phonsponsernameeno}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Purpose Of Visa :</p>
                    <p className='d-inline-block'>{data.application.purposeofvisa}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Profession :</p>
                    <p className='d-inline-block'>{data.application.profession}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Email :</p>
                    <p className='d-inline-block'>{data.application.email}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Education :</p>
                    <p className='d-inline-block'>{data.application.education}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Contract Duration Year :</p>
                    <p className='d-inline-block'>{data.application.contractyear}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>No Of Probation Months :</p>
                    <p className='d-inline-block'>{data.application.probationmonth}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Basic Salary :</p>
                    <p className='d-inline-block'>{data.application.basicsalary}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>House Allowance :</p>
                    <p className='d-inline-block'>{data.application.houseallowance}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Transport Allowance :</p>
                    <p className='d-inline-block'>{data.application.transportallowance}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>House Allowance :</p>
                    <p className='d-inline-block'>{data.application.foodallowance}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Other Allowance :</p>
                    <p className='d-inline-block'>{data.application.otherallowance}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Total Salary :</p>
                    <p className='d-inline-block'>{data.application.totalsalary}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Ticket Duration :</p>
                    <p className='d-inline-block'>{data.application.ticketduration}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Ticket Duration :</p>
                    <p className='d-inline-block'>{data.application.ticketduration}</p>
                </div>
                <div className="col-md-4 p-3">
                    <p className='d-inline-block pe-4'>Ticket Duration :</p>
                    <p className='d-inline-block'>{data.application.ticketduration}</p>
                </div>

            </div></div>
    )
}

export default VisaApplication