import React, { useEffect } from 'react'
import { MDBDataTable } from "mdbreact"
import { useGetProfileQuery } from '../store/api/AuthAPI';
import Loader from '../components/Loader';
import { useGetAllApllicationQuery, useUpdateApplicationMutation } from '../store/api/ApplicationAPI';
import { useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import toast from "react-hot-toast";

const Dashboard = () => {
    const navigate = useNavigate();

    const { isAuthorized } = useSelector((state) => state.user);

    const { isLoading } = useGetProfileQuery();
    const [updateApplication, { isError, isSuccess, error, data }] = useUpdateApplicationMutation()

    const { data: applicationAllData } = useGetAllApllicationQuery()


    useEffect(() => {
        if (isError) {
            toast.error(error.data.message);
        }
        if (isSuccess) {
            toast.success(data.message);
        }

        isAuthorized && navigate("/dashboard");
    }, [isAuthorized, isError]);

    const acceptHandler = async (e, _id) => {
        e.preventDefault();
        const body = {
            _id,
            apllicationstatus: "accept"
        }
        await updateApplication(body)
    }

    const rejectHandler = async (e, _id) => {
        e.preventDefault();
        const body = {
            _id,
            apllicationstatus: "reject"
        }
        await updateApplication(body)
    }


    const applicationData = {
        columns: [
            {
                label: "Visa No",
                field: "id",
                sort: "asc",
            },
            {
                label: "First Name",
                field: "firstname",
                sort: "asc",
            },
            {
                label: "Last Name",
                field: "lastname",
                sort: "asc",
            },
            {
                label: "Email",
                field: "email",
                sort: "asc",
            },
            {
                label: "Status",
                field: "apllicationstatus",
                sort: "asc",
            },
            {
                label: "Actions",
                field: "actions",
                sort: "disabled",
            },

        ],

        rows: []
    }

    applicationAllData?.result.map((application) => {

        applicationData?.rows.push({
            id: application._id,
            firstname: application.firstname,
            lastname: application.lastname,
            email: application.email.length > 15 ? `${application.email.substring(0, 15)}...` : application.email,
            apllicationstatus: <p className={`fw-bold ${application.apllicationstatus == "reject" ? "text-danger" : "text-success"}`} >{application.apllicationstatus}</p>,
            actions: (<>
                <button className='btn btn-primary ms-2' onClick={(e) => navigate(`/application/${application._id}`)}>
                    <i className='fa-solid fa-eye' ></i>
                </button>
                <button className='btn btn-success ms-2' onClick={(e) => acceptHandler(e, application._id)} >
                    <i class="fa-solid fa-check" ></i>
                </button>
                <button className='btn btn-danger ms-2' onClick={(e) => rejectHandler(e, application._id)}>
                    <i className='fa-solid fa-x'  ></i>
                </button>
            </>
            ),


        })
    })


    if (isLoading) {
        return <Loader />
    }

    if (!isAuthorized) {
        return navigate("/login");
    }
    return (
        <div>
            <div>
                <div className='container pt-2'>
                    <div className='d-flex justify-content-between'>
                        <p className='fs-4'>All Application</p>
                    </div>
                    <div className='mt-2'>
                        <MDBDataTable striped bordered hover data={applicationData} />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Dashboard